import React, {Component} from "react";
import {OraButton} from "eshop/components/OraCommonComponents";

class MulticartModificationModalComponent extends Component {

    onPrimaryButtonClick() {
        this.props.setShowSection("");
        this.props.closeAuthModal();
    }

    renderInfo() {
        return (
            <span dangerouslySetInnerHTML={{__html: this.props.descriptions.processChangeContent || this.props.defaultText}}/>
        );
    }

    getPrimaryButtonText() {
        return this.props.descriptions.processChangeButtonText || this.props.defaultButtonText;
    }

    render() {
        return (
            <div>
                <div className="u-padding-all-m u-small-padding-s">
                    <div className="l-row u-spacing-top-m">
                        <div className="l-col l-col-12">
                            {this.renderInfo()}
                        </div>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 large-offset-by-8">
                        <OraButton onClick={this.onPrimaryButtonClick.bind(this)} className="u-w-100">
                            {this.getPrimaryButtonText()}
                        </OraButton>
                    </div>
                </div>
            </div>
        );
    }
}

MulticartModificationModalComponent.defaultProps = {
    defaultText: "<span>Zweryfikowaliśmy Twoje dane odnośnie posiadania lini telefonicznej (telefon domowy) Pod wskazanym adresem do instalacji usługi. W ramach posiadanej lini zostanie odliczona opłata za utrzymanie łącza w wysokości 10 zł/mies. na kwotę abonamentu za usługę. Zaktualizowane ceny możesz zobaczyć w sekci <b>Podsumowanie opłat</b> przechodząc na krok <b>Dostawa i Płatność</b><br/></span>",
    defaultButtonText: "Kontynuuj zamówienie"
};

export default MulticartModificationModalComponent;